import React, { Component } from "react";
import { dati, AGGIORNAMENTO, COMMON_PROPS } from '../assets/dati';
import Grafico from "./Grafico";
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Grid from '@material-ui/core/Grid';
import moment from "moment";


let giorni = (ultimoMese) => {
    let etichette = [...new Set(dati.map(element => element.DateRep))];

    if (ultimoMese) {
        let unMeseFa = moment().subtract(3, 'weeks').toDate();

        etichette = etichette.filter(elem => {
            let dataRilevazione = moment(elem, "DD/MM/YYYY").toDate();
            return (dataRilevazione > unMeseFa);
        });
    }
    return etichette;
}



let recuperaDatasetPaese = (paese, tipo, ultimoMese) => {

    let datiPaese = dati.filter(element => element.CountryExp === paese);

    if (ultimoMese) {
        let unMeseFa = moment().subtract(3, 'weeks').toDate();

        datiPaese = datiPaese.filter(elem => {
            let dataRilevazione = moment(elem.DateRep, "DD/MM/YYYY").toDate();
            return (dataRilevazione > unMeseFa);
        });
    }

    let datiPaesePerTipo = datiPaese.map(element => element[tipo]);


    let l_borderColor = "";
    let l_backGroundColor = "";

    switch (paese) {
        case "Italy":
            l_borderColor = '#8000ff';
            l_backGroundColor = "#d9b3ff";
            break;

        case "Spain":
            l_borderColor = '#e68a00';
            l_backGroundColor = "#ffe0b3";
            break;

        case "France":
            l_borderColor = '#3399ff';
            l_backGroundColor = "rgba(75,192,192,0.4)";
            break;

        case "Germany":
            l_borderColor = '#00b33c';
            l_backGroundColor = "#ccffdd";
            break;

        case "China":
            l_borderColor = '#ff0066';
            l_backGroundColor = "#ffb3d1";
            break;

        default:
            l_backGroundColor = "#d9b3ff";
            l_borderColor = 'black';

    }


    let x = {
        label: paese,
        data: datiPaesePerTipo,

        backgroundColor: l_backGroundColor,
        borderColor: l_borderColor,

    };

    return (
        Object.assign(x, COMMON_PROPS)
    )
}



let graficoVuoto = () => {
    let ds = [];

    ds.push(recuperaDatasetPaese("Germany", "NewDeaths", true));
    ds.push(recuperaDatasetPaese("France", "NewDeaths", true));
    ds.push(recuperaDatasetPaese("Spain", "NewDeaths", true));
    ds.push(recuperaDatasetPaese("Italy", "NewDeaths", true));

    return ({
        labels: giorni(true),
        datasets: ds
    });
};





class Main extends Component {


    constructor(props) {
        super(props);



        this.state = {
            grafico: graficoVuoto(),
            tipo: "NewDeaths",
            ultimoMese: true,
            showItaly: true,
            showChina: false,
            showFrance: true,
            showGermany: true,
            showSpain: true,

        }

    }







    componentDidMount() {
    }







    ////////////////////////////////////////////////////////////////




    handleCheck = name => event => {

        let nomePaese = name.substring(4);
        let daAggiungere = event.target.checked;

        this.setState((prevState) => {

            let newState = prevState;
            newState[name] = daAggiungere;

            if (daAggiungere) {
                let esiste =
                    newState.grafico.datasets.some((elem) => {
                        return elem.label === nomePaese
                    })

                if (!esiste) {
                    let ds = recuperaDatasetPaese(nomePaese, prevState.tipo, prevState.ultimoMese);
                    newState.grafico.datasets.push(ds)
                }
            } else {

                newState.ultimoMese = prevState.ultimoMese;
                newState.grafico.labels = giorni(prevState.ultimoMese);

                let newDataSets = [];

                if (prevState.showGermany) {
                    newDataSets.push(recuperaDatasetPaese("Germany", prevState.tipo, prevState.ultimoMese));
                }
                if (prevState.showFrance) {
                    newDataSets.push(recuperaDatasetPaese("France", prevState.tipo, prevState.ultimoMese));
                }
                if (prevState.showSpain) {
                    newDataSets.push(recuperaDatasetPaese("Spain", prevState.tipo, prevState.ultimoMese));
                }
                if (prevState.showItaly) {
                    newDataSets.push(recuperaDatasetPaese("Italy", prevState.tipo, prevState.ultimoMese));
                }
                if (prevState.showChina) {
                    newDataSets.push(recuperaDatasetPaese("China", prevState.tipo, prevState.ultimoMese));
                }

                newState.grafico.datasets = newDataSets;


            }

            return newState;
        }
        );


    };



    handleChangeTipo = name => event => {

        let tipo = event.target.value;

        this.setState((prevState) => {
            let newState = prevState;

            newState.tipo = tipo;
            let newDataSets = [];
            if (prevState.showGermany) {
                newDataSets.push(recuperaDatasetPaese("Germany", tipo, prevState.ultimoMese));
            }
            if (prevState.showFrance) {
                newDataSets.push(recuperaDatasetPaese("France", tipo, prevState.ultimoMese));
            }
            if (prevState.showSpain) {
                newDataSets.push(recuperaDatasetPaese("Spain", tipo, prevState.ultimoMese));
            }
            if (prevState.showItaly) {
                newDataSets.push(recuperaDatasetPaese("Italy", tipo, prevState.ultimoMese));
            }
            if (prevState.showChina) {
                newDataSets.push(recuperaDatasetPaese("China", tipo, prevState.ultimoMese));
            }

            newState.grafico.datasets = newDataSets;

            return newState;
        }
        );

    };



    handleChangeUltimoMese = name => event => {

        let ultimoMese = event.target.checked;

        this.setState((prevState) => {

            let newState = prevState;

            newState.ultimoMese = ultimoMese
            newState.grafico.labels = giorni(ultimoMese);

            let newDataSets = [];

            if (prevState.showGermany) {
                newDataSets.push(recuperaDatasetPaese("Germany", prevState.tipo, ultimoMese));
            }
            if (prevState.showFrance) {
                newDataSets.push(recuperaDatasetPaese("France", prevState.tipo, ultimoMese));
            }
            if (prevState.showSpain) {
                newDataSets.push(recuperaDatasetPaese("Spain", prevState.tipo, ultimoMese));
            }
            if (prevState.showItaly) {
                newDataSets.push(recuperaDatasetPaese("Italy", prevState.tipo, ultimoMese));
            }
            if (prevState.showChina) {
                newDataSets.push(recuperaDatasetPaese("China", prevState.tipo, ultimoMese));
            }

            newState.grafico.datasets = newDataSets;


            return newState;
        }
        );

    };





    renderCheck = nome => {
        let nomeCheck = "show" + nome;
        let valoreStato = this.state[nomeCheck];

        return (

            <FormControlLabel
                control={
                    <Switch checked={valoreStato}
                        onChange={this.handleCheck(nomeCheck)} value={nomeCheck}
                        color="primary"
                    />
                }
                label={nome}
            />


        );
    };

    ////////////////////////////////////





    render() {

        return (
            <Grid
                container
                direction="column"
                justify="space-evenly"
                alignItems="stretch"
            >
                <h1 className="titolo">Situazione contagi / decessi per Coronavirus al {AGGIORNAMENTO}</h1>

                <fieldset className="opzioni">
                    <legend>Opzioni</legend>

                    <FormGroup row>
                        {this.renderCheck("Italy")}
                        {this.renderCheck("France")}
                        {this.renderCheck("China")}
                        {this.renderCheck("Germany")}
                        {this.renderCheck("Spain")}
                    </FormGroup>

                    <RadioGroup row value={this.state.tipo} name="tipo" onChange={this.handleChangeTipo("tipo")}>
                        <FormControlLabel value="NewDeaths" control={<Radio />} label="Nuovi decessi" />
                        <FormControlLabel value="NewConfCases" control={<Radio />} label="Nuovi contagi" />
                    </RadioGroup>
                    <FormGroup row>

                        <FormControlLabel
                            control={
                                <Switch checked={this.state.ultimoMese}
                                    onChange={this.handleChangeUltimoMese("ultimoMese")} value="ultimoMese" name="ultimoMese"
                                    color="secondary"
                                />
                            }
                            label="Limita i dati alle ultime tre settimane"
                        />
                    </FormGroup>


                </fieldset>

                <div className="grafico">
                    <Grafico data={this.state.grafico}></Grafico>
                </div>
                <p className="alert">Attenzione! L'applicazione  ancora in  fase di sviluppo per cui sono possibili errori e malfunzionamenti</p>

                <p><span className="avviso">I dati sono stati resi disponibili
                da <br></br><a href="https://www.ecdc.europa.eu/en/publications-data/download-todays-data-geographic-distribution-covid-19-cases-worldwide">European Centre
                for Disease Prevention and Control</a></span>
                    <br></br><br></br>

                 Chart CV - Versione 0.3<br></br>
                    <a href="mailto:franco.vuoto@gmail.com">Franco Vuoto</a> - Bologna, 14/03/2020</p>

                <p> </p>



            </Grid>
        );
    }
}

export default Main;

